<template>
  <v-drawer width="30rem" @close="handleClose">
    <v-form :data="value">
      <v-input name="name" label="" :rules="[rule.required, rule.min(2)]" />

      <v-input name="description" label="" multi-line />

      <v-input
        name="visibility"
        type="select"
        label=""
        style="width: 15rem"
        :default-value="1"
        :options="visibility"
      />

      <div style="margin-top: 2rem;">
        <v-submit @submit="handleSubmit"></v-submit>
        <ui-button style="margin-left: 1rem;" @click="handleClose"></ui-button>
      </div>
    </v-form>
  </v-drawer>
</template>

<script>
import { fastClone } from '@/utils/clone'
import fetch from '@/utils/fetch'
import Rule from '@/utils/rule'

export default {
  props: {
    data: Object,
  },
  emits: ['close'],
  data() {
    return {
      value: fastClone(this.data),
      visibility: [
        { label: '', value: 0 },
        { label: '', value: 1 },
        { label: '', value: 2 },
      ],
      rule: Rule(),
    }
  },
  methods: {
    handleClose() {
      this.$emit('close')
    },
    handleSubmit() {
      fetch.post('/api/app', this.value).then(id => {
        this.$message.show('')
        this.$router.push(`/app/${id}/schema/0`)
      })
    },
  },
}
</script>

<style></style>
