<template>
  <div v-auth:page="1" class="user-container">
    <v-search v-model="filter" style="width: 20rem; margin-bottom: 2rem; padding: 0.5rem 0;" />

    <v-fab-add @click="showEdit = true" />

    <v-table :data="list">
      <v-table-col title="" name="name" />
      <v-table-col title="">
        <template #default="scope">
          {{ ['', ''][scope.role] }}
        </template>
      </v-table-col>
      <v-table-col title="" name="createdAt" />
      <v-table-col title="" name="lastLoginAt" />
      <v-table-col title="">
        <template #default="scope">
          {{ ['', ''][scope.status] }}
        </template>
      </v-table-col>
    </v-table>

    <edit v-if="showEdit" @close="handleClose" />
  </div>
</template>

<script>
import fetch from '@/utils/fetch'
import Edit from './edit.vue'

export default {
  components: {
    Edit,
  },
  data() {
    return {
      filter: '',
      showEdit: false,
      users: [],
    }
  },
  computed: {
    list() {
      if (!this.filter) return this.users
      return this.users.filter(u => u.name.indexOf(this.filter) > -1)
    },
  },
  created() {
    fetch.get('/api/user/all').then(res => {
      this.users = res
    })
  },
  methods: {
    handleClose(data) {
      this.showEdit = false
      if (data) this.users.unshift(data)
    },
  },
}
</script>

<style lang="scss" scoped>
.user-container {
  position: relative;
  flex: 1;
  height: calc(100vh - 3.5rem);
  overflow: auto;
  padding: 2rem;
}
</style>
